import classNames from 'classnames'
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import Earth from './components/Earth'
import Footer from './components/Footer'
import MenuButton from './components/MenuButton'
import Popup from './components/Popup'
import Sidebar from './components/Sidebar'
import styles from './scss/App.module.scss'
import { io } from "socket.io-client"

const App = () => {

  const [sidebarActive, setSidebarAcitve] = useState(false)

  const [socket, setSocket] = useState(null)

  const speed = useSelector(state => state.speed)
  const timeDirectionStatus = useSelector(state => state.timeDirectionStatus)

  useEffect(() => {
    const newSocket = io('http://103.246.146.95:8000')
    setSocket(newSocket)
    return () => newSocket.close()
  }, [])

  useEffect(() => {

    const handleWindowClick = e => {
      if (!e.target.closest('.'+styles.sidebar) && !e.target.closest('.'+styles.menuBtn))
        return setSidebarAcitve(false)
    }

    window.addEventListener('click', handleWindowClick)
    return () => window.removeEventListener('click', handleWindowClick)
  }, [])

  useEffect(() => {
    console.log('spped_emit')
    if (!socket) return
    socket.emit('get_detail_data_about_object', {'speed': speed})
  }, [speed, socket])

  useEffect(() => {
    console.log('status')
    if (!socket) return
    socket.emit('get_detail_data_about_object', {'status': timeDirectionStatus})
  }, [timeDirectionStatus, socket])
  
  if (!socket) return <></>
  return (
    <>

      <Earth socket={socket} />

      <Popup />
      
      <div className={styles.root}>

        {!sidebarActive && <MenuButton onClick={() => setSidebarAcitve(true)} className={styles.menuBtn} />}

        <Footer 
          socket={socket}
          className={styles.footer} 
        />
        
        <Sidebar 
          className={classNames(
            styles.sidebar, 
            sidebarActive && styles['sidebar--open']
          )} 
          onExit={() => setSidebarAcitve(false)}
        />

      </div>
    </>
  )
}

export default App
