import React, { useEffect, useState } from 'react'
import * as WorldWind from '@nasaworldwind/worldwind'
import './index.css'
import { useDispatch, useSelector } from 'react-redux'
import { setTime } from '../redux/actions/timeActions'
import { setDate } from '../redux/actions/dateActions'

const Earth = React.memo(({
  socket
}) => {

  const dispatch = useDispatch()

  useEffect(() => {
    if (!socket) return

    console.log('start')
    
    socket.on('connect', () => {
      console.log(socket.id)
    }) 
    
    console.log('status')
    socket.emit('get_detail_data_about_object', {'status': 'stream'})
    
    var wwd = new WorldWind.WorldWindow("canvasOne")

    wwd.addLayer(new WorldWind.BMNGOneImageLayer())
    wwd.addLayer(new WorldWind.BMNGLandsatLayer())
    wwd.addLayer(new WorldWind.BingAerialLayer())

    wwd.addLayer(new WorldWind.AtmosphereLayer(wwd))

    var modelLayer = new WorldWind.RenderableLayer()
    wwd.addLayer(modelLayer)

    socket.on('location', data1 => {

      console.log(JSON.parse(data1))

      let data = JSON.parse(data1)
      
      const setData = async () => {
  
        await dispatch(setDate(data.date))

        const time = data.time.split(':')

        await dispatch(setTime({
          hh: time[0],
          mm: time[1],
          ss: time[2]
        }))

        wwd.layers = wwd.layers.filter(d => d.displayName !== "Custom Placemark")
        
        data.data.forEach(d => {

          var placemarkAttributes = new WorldWind.PlacemarkAttributes(null)
          if(d.type === 'debris'){
            placemarkAttributes.imageColor = WorldWind.Color.RED
          }
          else if(d.type === 'rocket body'){
            placemarkAttributes.imageColor = WorldWind.Color.WHITE
          }
          else if(d.type === 'satellite'){
            placemarkAttributes.imageColor = WorldWind.Color.GREEN
          }
          else{
            placemarkAttributes.imageColor = WorldWind.Color.YELLOW
          }

            
            


  
          var placemarkPosition = new WorldWind.Position(d.x, d.y, d.z * 1000)


          var placemark = new WorldWind.Placemark(placemarkPosition, false, placemarkAttributes)
          
          
          var placemarkLayer = new WorldWind.RenderableLayer("Custom Placemark")
          
          placemarkLayer.addRenderable(placemark)
          
          wwd.addLayer(placemarkLayer)
          
          wwd.redraw()
        })
        

      }
      
      setData()
    })

    return () => socket.on('disconnect', () => {
      console.log(socket.connected)
    })

  }, [dispatch, socket])

  return (
    <canvas id="canvasOne" width={window.innerWidth + 'px'} height={window.innerHeight + 'px'}>
        Your browser does not support HTML5 Canvas.
    </canvas>
  )
})

export default Earth
